// URL()
// let indexUrl = '/api/index';
//api

// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
    // params  
    let index = () => vm.$u.get('/api/index');
    
    //API
    let authLogin = params => vm.$u.post('/api/auth/login',params);//
	 let authRegister = params => vm.$u.post('/api/auth/register',params);//
	let  authLogout = () => vm.$u.post('/api/auth/logout');//
	let  authOssToken=() => vm.$u.get('/api/auth/oss/token');//OSStoken
    //API
    let userInfo = () => vm.$u.get('/api/user');//
	let userInfoUpdate = params  => vm.$u.put('/api/user',params);//
	let uploadUserAvatar = params => vm.$u.post("/api/user/avatar",params); //
	//API
	let getGoodsCategory = params => vm.$u.get("/api/goods", params); //
	let getGoodsDetail = params => vm.$u.get(`/api/goods/${params}`); //
	let handleGoodCollect = params => vm.$u.post(`/api/collects/goods/${params}`); //
	// API
	let handleAddCart = params => vm.$u.post("/api/carts", params); //
	let getCartList = () => vm.$u.get(`/api/carts`); //()
	let getCartGoods = () => vm.$u.get(`/api/carts?include=goods`); //()
	let changeGoodSelect = params => vm.$u.patch("/api/carts/checked", { cart_ids: params }); //
	let handleGoodNum = (cart, num) => vm.$u.put(`/api/carts/${cart}`, { num }); //
	let handleDeleteGood = (cart) => vm.$u.delete(`/api/carts/${cart}`); //
	
	//API
	let handleCheckOrder = () => vm.$u.get("/api/orders/preview"); //
	let handleSubmitOrder = (params) => vm.$u.post("/api/orders", params); //
	let checkOrderList = () => vm.$u.get(`/api/orders`); //
	let checkOrderDetail = (order) => vm.$u.get(`/api/orders/${order}`); //(404)
	let getLogisticsStatus = (order) => vm.$u.get(`/api/orders/${order}/express`); //
	let handleConfirmOrder = (order) => vm.$u.get(`/api/orders/${order}/confirm`); //
	let handleEvaluateOrder = (order) => vm.$u.get(`/api/orders/${order}/comment`); //
	
	// API
	let getPayQRcode = (order) => vm.$u.get(`/api/orders/${order}/pay`); //
	let getPayStatus = (order) => vm.$u.get(`/api/orders/${order}/status`); //
	
	// API
	let getCollections = () => vm.$u.get(`/api/collects`); //
	let handleCollect = (id) => vm.$u.post(`/api/collects/goods/${id}`); //
	
	// API
	let getLocationData = () => vm.$u.get("/api/city"); // 
	let addAddress = (params) => vm.$u.post("/api/address", params); // 
	let getAddressList = () => vm.$u.get("/api/address"); // 
	let checkAddressDetail = (address) => vm.$u.get(`/api/address/${address}`); // (HTTP)
	let updateAddress = (id, params) => vm.$u.put(`/api/address/${id}`, params); // ()
	let handleDelAddress = (id) => vm.$u.delete(`/api/address/${id}`); // let
	
	
    // vm.$u.api(vmthisthis.$u.api)
    vm.$u.api = {
	index,
	authLogin,
	authRegister,
	authLogout,
	authOssToken,
	userInfo,
	userInfoUpdate,
	uploadUserAvatar,
	getGoodsCategory,
	getGoodsDetail,
	handleGoodCollect,
	handleAddCart,
	getCartList,
	getCartGoods,
	changeGoodSelect,
	handleGoodNum,
	handleDeleteGood,
	handleCheckOrder,
	handleSubmitOrder,
	checkOrderList,
	checkOrderDetail,
	getLogisticsStatus,
	handleConfirmOrder,
	handleEvaluateOrder,
	getPayQRcode,
	getPayStatus,
	getCollections,
	handleCollect,
	getLocationData,
	addAddress,
	getAddressList,
	checkAddressDetail,
	updateAddress,
	handleDelAddress};
};

export default {
    install
};