// vmvuethisvuextoken
const install = (Vue, vm) => {
    // 
    Vue.prototype.$u.http.setConfig({
     	baseUrl: 'https://api.shop.eduwork.cn', // 
     	// method: 'GET',
     	// jsonJSON.parse()
     	dataType: 'json',
     	showLoading: true, // loading
     	loadingText: '...', // loading
     	loadingTime: 800, // ms
     	originalData: true, // 
     	loadingMask: true, // loading
  
     
    });
	   
    // Token
    Vue.prototype.$u.http.interceptor.request = (config) => {
      //uViewvuex$store.state
        // config.header.token = vm.$store.state.token;
        config.header.Authorization = `Bearer ${vm.vuex_token}` 
		 
    return config;
    };
	
    // 
    Vue.prototype.$u.http.interceptor.response = (res) => {
        //
        const {statusCode,data} = res;
        if(statusCode < 400) {
            // rescoderesult
            // res.resultthis.$u.post(url).then(res => {})thenres
            // originalDatatrue
            return data;
        } else if(statusCode == 400) {
            // 
            vm.$u.toast(data.message);
            return false;
        }
        else if(statusCode == 401) {
            //401token
           	if (data.message == 'Unauthorized'){
                vm.$u.toast('');
            }else {
				//api
              vm.$u.utils.isLogin()
            }
            
            // 401token
          
            return false;
        }
        else if(statusCode = 422) {
            //
            const {errors} = data;
            vm.$u.toast(Object.value(errors)[0][0]);
            return false;
        }
        else {
            // falsePromisereject
            // this.$u.post(url).then().catch(res=>{})catchres
            return false;
        }
    };
  // patch
  vm.$u.patch = (url, params = {}, header = {}) => {
    // postpatch
    const _params = {
      ...params,
      _method: "PATCH",
    };
    return vm.$u.post(url, _params, header);
  };
};

export default {
    install
};